import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/login'
    },
    {
      path: '/index',
      component: resolve => require(['../frame/frame.vue'], resolve),
      children: [
        {
          path: '/',
          component: resolve => require(['../components/Index.vue'], resolve)
        },
        {
          path: '/403',
          component: resolve => require(['../components/403.vue'], resolve)
        },
        {
          path: '/authsManage.usersManage',
          component: resolve => require(['../modules/authsManage/usersManage.vue'], resolve)
        },
        {
          path: '/authsManage.companiesManage',
          component: resolve => require(['../modules/authsManage/companiesManage.vue'], resolve)
        },
        {
          path: '/authsManage.divisionsManage',
          component: resolve => require(['../modules/authsManage/divisionsManage.vue'], resolve)
        },
        {
          path: '/authsManage.groupsManage',
          component: resolve => require(['../modules/authsManage/groupsManage.vue'], resolve)
        },
        {
          path: '/authsManage.rolesManage',
          component: resolve => require(['../modules/authsManage/rolesManage.vue'], resolve)
        },
        {
          path: '/orderManage.order',
          component: resolve => require(['../modules/orderManage/Order.vue'], resolve)
        },
        {
          path: '/orderManage.windCtrlAudit',
          component: resolve => require(['../modules/orderManage/windCtrlAudit.vue'], resolve)
        },
        {
          path: '/orderManage.windCtrlCEOAudit',
          component: resolve => require(['../modules/orderManage/windCtrlCEOAudit.vue'], resolve)
        },
        {
          path: '/orderManage.CSDAudit',
          component: resolve => require(['../modules/orderManage/CSDAudit.vue'], resolve)
        },
        {
          path: '/orderManage.FDAudit',
          component: resolve => require(['../modules/orderManage/FDAudit.vue'], resolve)
        },
        {
          path: '/carManage.carInfo',
          component: resolve => require(['../modules/carManage/carInfo.vue'], resolve)
        },
        {
          path: '/carManage.carApply',
          component: resolve => require(['../modules/carManage/carApply.vue'], resolve)
        },
        {
          path: '/tongji.customer',
          component: resolve => require(['../modules/tongji/customer.vue'], resolve)
        },
        {
          path: '/tongji.recive',
          component: resolve => require(['../modules/tongji/recive.vue'], resolve)
        },
        {
          path: '/tongji.loan',
          component: resolve => require(['../modules/tongji/loan.vue'], resolve)
        },
        {
          path: '/tongji.should_receive',
          component: resolve => require(['../modules/tongji/should_receive.vue'], resolve)
        }
      ]
    },
    {
      path: '/login',
      component: resolve => require(['../frame/login.vue'], resolve)
    },
  ]
})



